.ThaiCitizenIDGenerator {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .ThaiCitizenIDGenerator-input {
    display: flex;
    align-items: center;
    margin: 20px 0;
  }
  
  .ThaiCitizenIDGenerator-input label {
    margin-right: 10px;
  }
  
  .ThaiCitizenIDGenerator-input input[type="number"] {
    width: 60px;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .ThaiCitizenIDGenerator-output {
    margin: 20px 0;
  }
  
  button {
    background-color: #4caf50;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
  }
  
  .ThaiCitizenIDGenerator-textarea {
    width: 100%;
    height: 300px;
    padding: 10px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    resize: none;
  }
  
  .info-box {
    display: none;
    background-color: #4caf50;
    color: white;
    padding: 10px;
  }
  
  .citizen-id-value.selected {
    background-color: #b2ff59;
  }
  